import axios from 'axios';
import jsonp from 'assets/js/jsonp';

import { TIMEOUT, jsonpOption } from './congif';

// 获取热门搜索数据 axios ；
export const getSearchHotKeyword = () => {
  return axios
    .get('http://www.imooc.com/api/search/hot', {
      timeout: TIMEOUT
    })
    .then(res => {
      res = res.data.hotKeyWord;
      if (res && res.owner) {
        return res.owner;
      }

      throw new Error('没有获取到数据！');
    })
    .catch(err => {
      if (err) {
        console.log(err);
      }
  })
    .then(res => {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve(res);
        }, 1000);
      });
  });
};

// 获取热门搜索数据 jsonp
export const getSearchResult = keyword => {
  const url = 'https://suggest.taobao.com/sug';
  const params = {
    q: keyword,
    code: 'utf-8',
    area: 'c2c',
    nick: '',
    sid: null
  };
  return jsonp(url, params, jsonpOption)
    .then(res => {
      if (res.result) {
        return res.result;
      }

      throw new Error('没有成功获取到数据！');
    })
    .catch(err => {
      if (err) {
        console.log(err);
      }
  })
    .then(res => {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve(res);
        }, 1000);
      });
  });
};



